*{
     font-family:  Helvetica,Arial, sans-serif;
     color: grey; 
}

body{

     background-color: black;
     padding: 60px; 
}

input{
     font-size: 1.2rem;
     width: 100%;
     color: grey; 
}

.card-grid{
     display: flex; 
     flex-direction: row; 
     flex-wrap: wrap;
}


.card{
     align-content: center;
     border: 1px solid gray; 
     border-radius: 6px; 
     margin-bottom: 10px; 
     margin-right: 10px; 
     overflow: hidden;
}

.card p {
     padding: 5px;       
     font-style: italic;
     text-align: center; 
}

.card img{
     max-height: 170px;
     max-width: 170px; 
     align-content: center;
     margin: 1rem; 
     width: 96%;
}
.Contenedor {

     text-align: center;
}